import React from 'react';
import classes from './Login.module.scss';
import { Formik, Form, ErrorMessage, Field } from 'formik';
import { withRouter } from 'react-router-dom';
import * as yup from 'yup';

const validationSchema = yup.object({
	username: yup.string('یوزر درست وارد نشده').typeError('یوزر وارد نشده').required('یوزر وارد نشده'),
	password: yup.string('رمز درست وارد نشده').typeError('رمز وارد نشده').required('رمز وارد نشده')
});

function Login({ history }) {
	const onSubmit = (e) => {
		localStorage.setItem('auth', true);
		history.push('/');
	};
	const initialValues = {
		username: '',
		password: ''
	};
	return (
		<div className={classes.login}>
			<Formik initialValues={initialValues} onSubmit={onSubmit} validationSchema={validationSchema}>
				{(props) => {
					return (
						<div className="d-flex flex-column  align-items-center">
							<img alt="login" className={classes.pic} src={require('../../img/login.png')} />
							<Form>
								<div className="d-flex flex-column">
									<Field
										dir="rtl"
										className={classes.input}
										placeholder="یوزر"
										type="text"
										name="username"
									/>
									<p className="text-right ml-auto text-danger mr-1 mt-1">
										<ErrorMessage className="text-right ml-auto text-danger" name="username" />
									</p>
									<Field
										dir="rtl"
										className={classes.input}
										placeholder="پسورد"
										type="password"
										name="password"
									/>
									<p className="text-right ml-auto text-danger mr-1 mt-1">
										<ErrorMessage name="password" />
									</p>
									<button className={classes.submit} disabled={!props.isValid} type="submit">
										ورود به حساب
									</button>
								</div>
							</Form>
						</div>
					);
				}}
			</Formik>
		</div>
	);
}

export default withRouter(Login);
